import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { IPayPalConfig, ICreateOrderRequest } from 'ngx-paypal';
import { ToastrService } from 'ngx-toastr';
import { AppService } from '../app.service';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss']
})
export class CheckoutComponent implements OnInit {

  constructor(private toastr: ToastrService, private router: Router, private appService: AppService) {}

  public payPalConfig?: IPayPalConfig;
  public userInfo: any;

  ngOnInit(): void {
    this.userInfo = this.appService.getUserInfoFromLocalstorage();
    this.initConfig();
  }

  public showSuccess = false;

  private initConfig(): void {
    this.payPalConfig = {
    currency: 'USD',
    clientId: 'AbxHCukTPqiJPi2FeTgbxDltOYtIKsb6dT_GVL7-MN_s9MiSrlrXiN3niPf-oJYy6Yx9APFuYAThx6NL',
    createOrderOnClient: (data) => <ICreateOrderRequest>{
      intent: 'CAPTURE',
      purchase_units: [
        {
          amount: {
            currency_code: 'USD',
            value: '9.99',
            breakdown: {
              item_total: {
                currency_code: 'USD',
                value: '9.99'
              }
            }
          },
          items: [
            {
              name: 'Enterprise Subscription',
              quantity: '1',
              category: 'DIGITAL_GOODS',
              unit_amount: {
                currency_code: 'USD',
                value: '9.99',
              },
            }
          ]
        }
      ]
    },
    advanced: {
      commit: 'true'
    },
    style: {
      label: 'paypal',
      layout: 'vertical'
    },
    onApprove: (data, actions) => {
      console.log('onApprove - transaction was approved, but not authorized', data, actions);
      actions.order.get().then(details => {
        console.log('onApprove - you can get full order details inside onApprove: ', details);
      });
    },
    onClientAuthorization: (data) => {
      console.log('onClientAuthorization - you should probably inform your server about completed transaction at this point', data);
      this.showSuccess = true;
      this.toastr.success('Transaction is successful!');
      let editData = {userId: this.userInfo.userId, isPremium: true};
      this.appService.editUserUpdatePremium(editData).subscribe(response => {
        console.log('User Premium updated!');
        this.userInfo.isPremium = true;
        this.appService.setUserInfoInLocalStorage(this.userInfo);
        this.router.navigate(['/profile']);
      }, err => {
        console.log(err);
      })
    },
    onCancel: (data, actions) => {
      console.log('OnCancel', data, actions);
      this.toastr.warning('Transaction is cancelled!');
    },
    onError: err => {
      console.log('OnError', err);
      this.toastr.error('Something went wrong in the transaction!');
    },
    onClick: (data, actions) => {
      console.log('onClick', data, actions);
    },
  };
  }

}
