import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { AppService } from '../app.service';

@Component({
  selector: 'app-about-style-one',
  templateUrl: './about-style-one.component.html',
  styleUrls: ['./about-style-one.component.scss']
})
export class AboutStyleOneComponent implements OnInit {

  constructor(private toastr: ToastrService, private appService: AppService) { }

  public email: any;

  ngOnInit(): void {
  }

  sendMail() {
    console.log(this.email);
    let mailData = {
      email: this.email,
      subject: "Welcome to Medibuddy Channel!",
      text: `<div style="text-align: left; margin-left: 10px; font-family: Roboto, sans-serif;">
      <h2>Hey!</h2>
    </div><br>
    <div style="text-align: center; font-family: Roboto, sans-serif;">
      <h1>Welcome to our Medibuddy channel! You will receive our updates and new changes everytime we make!</h1><br>
      <p>Register to create appointments, ask questions, interact with doctors and a lot more!</p>
    </div>`
    }
    this.appService.sendMail(mailData).subscribe(response => {console.log('Mail Sent!'), this.toastr.success('Subscribed!')}, err => console.log(err));
  }

}
